import { useRequest } from 'ahooks';
import React, { useEffect, useState } from 'react';
import request from '../comment/request';
const isZero = (nowPrice, last_close) => {
  if (!last_close) {
    return nowPrice;
  }
  if (parseFloat(nowPrice) === 0) {
    return last_close;
  }
  return nowPrice;
};
const getDownDataWhenListedCompanies = () => {
  return request('/govwebapi/gov/web/api/bs-citycenter/index/wss/quotation/get', {
    params: {
      codes: '000011.SH,000012.SH,INCI.SHF,AU.SHF,IF.CFE,SHIBORON.IR,CNYX.IB',
      date: '20201116',
    },
  }).then(res => {
    //   console.log('zjf--------------------res:', res);
    if (res) {
      const temptab4 = [
        {
          key: '000011.SH',
          name: '上证基金指数',
        },
        {
          key: '000012.SH',
          name: '上证国债',
        },
        {
          key: 'INCI.SHF',
          name: '上期工业金属期货价格指数',
        },
        {
          key: 'AU.SHF',
          name: 'SHFE黄金',
        },
        {
          key: 'IF.CFE',
          name: '沪深300期货',
        },
        {
          key: 'SHIBORON.IR',
          name: '上海银行间同业拆放利率',
        },
        {
          key: 'CNYX.IB',
          name: '人民币汇率指数',
        },
      ];
      temptab4.forEach(item => {
        const t = res[item.key];
        const newData = [];
        newData.push(isZero(t?.nowPrice, t?.last_close));
        newData.push(t?.upAndDown);
        newData.push(t?.upAndDownRate);
        newData.push(t?.yearToDateRate);
        item.data = newData;
      });
      // console.log('zjf---temptab4:', temptab4);
      return Promise.resolve(temptab4);
    }
  });
};
const tes = new RegExp(/^\-/);
export default function FootData() {
  const [outData, setOutData] = useState(false);
  const { data, loading, run, cancel } = useRequest(getDownDataWhenListedCompanies, {
    // manual: true,
    pollingInterval: 5000,
    pollingWhenHidden: false,
  });
  useEffect(() => {
    setOutData(data);
  }, [data]);
  return (
    <>
      {outData && outData.length > 0
        ? outData.map((item, index) => {
            return (
              <div className="foot-data-block" key={index}>
                <div style={{ color: '#2BF9FF' }}>{item.name + '：'}</div>
                <div className={tes.test(item.data) ? 'value-green' : 'value-red'}>
                  {item.data && item.data[0] ? item.data[0] : '暂无'}
                </div>
              </div>
            );
          })
        : ''}
    </>
  );
}

FootData.propTypes = {};
FootData.defaultProps = {};
