import { combineReducers } from 'redux';
// import { routerReducer } from 'react-router-redux';
import { connectRouter } from 'connected-react-router'
import history from './history';
import commonReducer from '../features/common/redux/reducer';
import zhangjiangReducer from '../features/zhangjiang/redux/reducer';
import jingrongReducer from '../features/jingrong/redux/reducer';
import dashboardReducer from '../features/dashboard/redux/reducer';
import teReducer from '../features/te/redux/reducer';

// NOTE 1: DO NOT CHANGE the 'reducerMap' name and the declaration pattern.
// This is used for Rekit cmds to register new features, remove features, etc.
// NOTE 2: always use the camel case of the feature folder name as the store branch name
// So that it's easy for others to understand it and Rekit could manage them.

const reducerMap = {
  router: connectRouter(history),
  common: commonReducer,
  zhangjiang: zhangjiangReducer,
  jingrong: jingrongReducer,
  dashboard: dashboardReducer,
  te: teReducer,
};

export default combineReducers(reducerMap);
