import React, { useEffect, useState } from 'react';
import req from './comment/request';
import { WindChart } from '../common/components/govChart';
import TableCard from './TableCard';
// import DashTable from '../dashboard/components/DashTable';
// import PropTypes from 'prop-types';

export default function Zhaotoubiao(props) {
  const { cityCode } = props;
  const [biddingProgress, setBiddingProgress] = useState([]);
  const [procureMethod, setProcureMethod] = useState([]);
  const [biddingAmount, setBiddingAmount] = useState([]);
  const [category, setCategory] = useState([]);
  useEffect(() => {
    let startTime = new Date(new Date().setFullYear(new Date().getFullYear() - 1)).format(
      'yyyyMMdd',
    );
    let endTime = new Date().format('yyyyMMdd');

    //招投标进度
    req('/govwebapi/gov/web/api/integration/dataset/datainfo', {
      params: {
        dataSetCode: '7871887c640311eaaa93fa163ed85ccc',
        endDate: endTime,
        sectorIds: cityCode,
        startDate: startTime,
      },
    }).then(result => {
      let data = [];
      if (result.dataList.length !== 0) {
        for (let r of result.dataList) {
          data.push({
            name: r.progressTypeName,
            value: r.statics,
          });
        }
        setBiddingProgress(data);
      } else {
        setBiddingProgress([{ name: '招投标进度', value: 0 }]);
      }
    });

    //采购方式
    req('/govwebapi/gov/web/api/integration/dataset/datainfo', {
      params: {
        dataSetCode: '78718a9d640311eaaa93fa163ed85ccc',
        endDate: endTime,
        sectorIds: cityCode,
        startDate: startTime,
      },
    }).then(result => {
      let data = [];
      if (result.dataList.length !== 0) {
        for (let r of result.dataList) {
          data.push({
            name: r.purchaseWayName,
            value: r.statics,
          });
        }
        setProcureMethod(data);
      } else {
        setProcureMethod([{ name: '采购方式', value: 0 }]);
      }
    });
    //采购金额
    req('/govwebapi/gov/web/api/integration/dataset/datainfo', {
      params: {
        dataSetCode: '78718ec2640311eaaa93fa163ed85ccc',
        endDate: endTime,
        sectorIds: cityCode,
        startDate: startTime,
      },
    }).then(result => {
      let data = [];
      if (result.dataList.length !== 0) {
        for (let r of result.dataList) {
          data.push({
            name: r.purchaseMoneyName,
            value: r.statics,
          });
        }
        setBiddingAmount(data);
      } else {
        setBiddingAmount([{ name: '采购金额', value: 0 }]);
      }
    });

    //品目类别
    req('/govwebapi/gov/web/api/integration/dataset/datainfo', {
      params: {
        dataSetCode: '78718cc5640311eaaa93fa163ed85ccc',
        endDate: endTime,
        sectorIds: cityCode,
        startDate: startTime,
      },
    }).then(result => {
      let data = [];
      if (result.dataList.length !== 0) {
        for (let r of result.dataList) {
          data.push({
            name: r.categoryTypeName,
            value: r.statics,
          });
        }
        setCategory(data);
      } else {
        setCategory([{ name: '品目类别', value: 0 }]);
      }
    });
  }, [cityCode]);

  return (
    <div className="dashboard-zhaotoubiao">
      <div className="content-middle">
        <div>
          <TableCard
            title="招投标项目统计 - 招投标进度"
            table={
              biddingProgress.length !== 0 ? (
                <WindChart
                  option={{
                    tooltip: {
                      trigger: 'item',
                    },
                    legend: {
                      type: 'scroll',
                      orient: 'horizontal',
                      left: 'center',
                      bottom: '0%',
                    },
                    series: [
                      {
                        type: 'pie',
                        left: 'center',
                        radius: '60%',
                        center: ['50%', '48%'],
                        data: biddingProgress,
                        emphasis: {
                          itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)',
                          },
                        },
                      },
                    ],
                  }}
                />
              ) : (
                'loading'
              )
            }
          />
        </div>
        <div>
          <TableCard
            title="招投标项目统计 - 采购方式"
            table={
              procureMethod.length !== 0 ? (
                <WindChart
                  option={{
                    tooltip: {
                      trigger: 'item',
                    },
                    legend: {
                      type: 'scroll',
                      orient: 'horizontal',
                      left: 'center',
                      bottom: 0,
                    },
                    series: [
                      {
                        type: 'pie',
                        // roseType: 'area',
                        left: 'center',
                        radius: ['35%', '60%'],
                        center: ['50%', '48%'],
                        data: procureMethod,
                        emphasis: {
                          itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)',
                          },
                        },
                      },
                    ],
                  }}
                />
              ) : (
                'loading'
              )
            }
          />
        </div>
        <div>
          <TableCard
            title="招投标项目统计 - 品目类别"
            table={
              category.length !== 0 ? (
                <WindChart
                  option={{
                    tooltip: {
                      trigger: 'item',
                    },
                    legend: {
                      type: 'scroll',
                      orient: 'horizontal',
                      left: 'center',
                      bottom: 0,
                      data: category.map(item => {
                        if (item.name !== '') {
                          return item.name;
                        }
                      }),
                    },
                    series: [
                      {
                        type: 'pie',
                        avoidLabelOverlap: true,
                        left: 'center',
                        radius: '60%',
                        center: ['50%', '48%'],
                        z: 1,
                        data: category,
                        emphasis: {
                          itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)',
                          },
                        },
                      },
                      {
                        type: 'pie',
                        avoidLabelOverlap: true,
                        left: 'center',
                        radius: '40%',
                        hoverAnimation: false,
                        center: ['50%', '48%'],
                        animation: false,
                        label: { show: false },
                        z: 2,
                        silent: false,
                        // center: ['40%', '50%'],
                        data: [
                          {
                            name: '1',
                            value: 1,
                            itemStyle: {
                              normal: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                color: '#364660',
                                opacity: 0.5,
                              },
                              emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                color: '#364660',
                                opacity: 0.5,
                              },
                            },

                            tooltip: {
                              show: false,
                            },
                          },
                        ],
                      },
                      {
                        type: 'pie',
                        avoidLabelOverlap: true,
                        left: 'center',
                        radius: '20%',
                        center: ['50%', '48%'],
                        animation: false,
                        label: { show: false },
                        legendHoverLink: false,
                        z: 3,
                        // center: ['40%', '50%'],
                        data: [
                          {
                            name: '1',
                            value: 1,
                            itemStyle: {
                              normal: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                color: '#D28367',
                              },
                              emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)',
                                color: '#D28367',
                              },
                            },
                            tooltip: {
                              show: false,
                            },
                          },
                        ],
                      },
                    ],
                  }}
                />
              ) : (
                'loading'
              )
            }
          />
        </div>
        <div>
          <TableCard
            title="招投标项目统计 - 采购金额"
            table={
              biddingAmount.length !== 0 ? (
                <WindChart
                  option={{
                    tooltip: {
                      trigger: 'item',
                    },

                    legend: {
                      type: 'scroll',
                      orient: 'horizontal',
                      left: 'center',
                      bottom: '0%',
                    },
                    series: [
                      {
                        type: 'pie',
                        avoidLabelOverlap: true,
                        left: 'center',
                        radius: ['35%', '60%'],
                        center: ['50%', '48%'],
                        data: biddingAmount,

                        emphasis: {
                          itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)',
                          },
                        },
                      },
                    ],
                  }}
                />
              ) : (
                'loading'
              )
            }
          />
        </div>
      </div>
    </div>
  );
}

Zhaotoubiao.propTypes = {};
Zhaotoubiao.defaultProps = {};
